import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';
import Alert from '@mui/material/Alert';



export const ContactUs = () => {
  const form = useRef();

  const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm('service_lguaoy9', 'template_8xt6vva', form.current, {
        publicKey: 'LfdVqs_cE6QAOo9ea',
      })
      .then(
        () => {
          console.log('SUCCESS!');
          <Alert severity="success">Email sent successfully!</Alert>
        },
        (error) => {
          console.log('FAILED...', error.text);
        },
      );
  };

  return (
    <form ref={form} onSubmit={sendEmail} className='form'>
       
      <div className='fields'>
        <label className='nameString' for="from_name">From:</label>
        <input type="text" name="from_name" id="from_name" placeholder="Jane"/>
      </div>
      
      <label className='nameString' for="date">Appointment Date </label>
      <input  type="text" name="date" id="date" placeholder="02-10-2024" required/>

      <label className='nameString' for="message">Message</label>
      <input className='message' type="text" name="message" id="message"  />

    
      <label className='nameString' for="contact_number">Contact Number</label>
      <input type="text" name="contact_number" id="contact_numberr" placeholder="+1 (786) 633-5515" required/>

      <label className='nameString' for="user_email">Email:</label>
      <input type="text" name="user_email" id="user_email" placeholder="jane@acme.com" required />

      <button className='formButton' type="submit">Submit</button>
    </form>
  );
};

export default ContactUs;